.footer {
  width: 85.5%;

  position: fixed;
  bottom: 66px;
  right: 6.3%;
  z-index: 200;

  @include viewport--md {
    width: 87%;
    min-height: 66px;
    bottom: 28px;

    opacity: 0;
    animation: comming-footer 2s 2s forwards;
  }

  // для iphone 5/se
  @media (max-width: 350px) {
    bottom: 10px;
  }
}

.footer__box {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
}

@keyframes comming-footer {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
